@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{box-sizing:border-box}html{font-family:'Roboto', sans-serif}.app__title{text-align:center}

.user{font-size:40px;font-weight:300;margin-bottom:10px}

.todo__status{color:red}.todo__status--completed{color:green}.todo__title{font-size:20px;font-weight:300}.todo__title::first-letter{text-transform:uppercase;font-size:1.3em;font-weight:400}.todo__title::after{content:"."}

.list{padding:0}.list__card{width:400px;margin:0 auto;text-align:center;list-style-type:none;border:2px solid;border-radius:10px;margin-bottom:20px}

